@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@800&family=Modak&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
    height: 100%;
}

body {
    height: 100%;
    background: black;
}

h1 {
    color: white;
    font-family: 'Dosis', sans-serif;
    /* font-family: 'Modak', cursive; */
}

#message {
    color: white;
    font-family: 'Dosis', sans-serif;
    text-align: center;
}

input {
    background-color: black;
    border: none;
    border-bottom: 2px white ridge;
    color: white;
    font-size: 30px;
    font-family: 'Dosis', sans-serif;
    width: 70px;
    text-align: center;
}

input:focus {
    outline: none;
}

#whole-center {
    width: content;
    padding-left: 35%;
    padding-top: 20%;
}

#current-time {
    color: white;
    font-size: 30px;
    font-family: 'Dosis', sans-serif;
    width: 70px;
    margin: auto;
    text-align: center;
}
